import React, { Component, useState } from "react"; // import from react

import { Window, App, View, Button, Text, TextInput, StyleSheet, TouchableOpacity } from "proton-native"; // import the proton-native components

const TodoList = (props) => {

  return (
    <View 
      style={{
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-around",
        alignItems: "center",
        backgroundColor: "#EEEEEE",
        padding: 10,
        borderBottomWidth: 1,
        borderBottomColor: "#666666"
      }} 
    >

    <Button onPress={() => props.checkTodo(props.todo.key)} title={props.todo.isChecked ? "checked" : "todo"} />

    <Text>{props.todo.name}</Text>
    <Button onPress={() => props.deleteTodo(props.todo.key)} title={`delete`} />

    </View>
  );
}

export default TodoList;